@import url(https://fonts.googleapis.com/css?family=Zilla+Slab&display=swap);
@import url(https://fonts.googleapis.com/css?family=Just+Me+Again+Down+Here|Reenie+Beanie|Amatic+SC&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.resume-card {
    position: relative;
    max-width: 725px;
    min-width: 725px;
    margin-left: -130px;
    padding-left: 130px;
    /*margin-top: -100px;*/
}

.resume-card > .title {
    color: #61dafb;
    margin-top: 0px;
    margin-bottom: 10px;
}

.resume-card > .title > .subtitle {
    color: #ccc;
}

.resume-card > .left {
    float:left;
    margin-top: 5px;
}

.resume-card > .right {
    text-align: left;
    font-size: 0.7em;
}
.resume-card a {
    display: inline-block;
    color: #AEECEF;
    text-decoration: none;
}

.resume-card a:hover {
    color: #2176AE;
}

.left + .right {
    margin-left: 135px;
}

.resume-card .timeline {
    float: right;
    font-size: 0.7em;
    color: #ccc;
}

.resume-card > .right > .contents {
    font-size: 0.7em;

    /* font-family: 'Zilla Slab', serif; */
    font-weight: normal;
}

.resume-card > .right > .contents .subtitle {
    font-size: 2.0em;
}

.skill-img {
    width: 25%;
    margin-right:10px;
    display: inline-block;
}

body {
    background-color: #282c34;
}

.App {
    text-align: center;
    background-color: #282c34;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.teaser {
    /*font-family: 'Amatic SC', cursive !important;*/
    max-width: 700px;
    font-size: 0.35em;
    text-align: justify;
    margin-left: auto;
    margin-right: auto;
    font-weight: normal;
    color: #FFF;
}

.App-article {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-Main {
    margin-left: 20%;
    margin-right: 20%;
}

.App-links {
    color: #61dafb;
    font-size: 14.07px;
}

.App-GroupName {
    width: 100%;
    text-align: center;
    position: absolute;
    font-size: 3em;
    top: 45%;
    left: -40%;
    transform: rotate(-90deg);
    color: #61dafb;
    font-family: 'Amatic SC', cursive !important;
    display: block;

}

.App-GroupName .App-SectionName {
    opacity: 0.2;
}

.App-GroupName a {
    cursor: pointer;
    display: inline-block;
    margin-left: 15px;
    margin-right: 15px;
    font-size: 0.3em;
    opacity: 0.2;
    vertical-align: middle;
}

.App-GroupName a:hover {
    opacity: 0.4;
}

.Card-navheader .subtitle {
    font-size: 32px !important;
}

.App-links a {
    color: #AEECEF;
    cursor: pointer;
    display: inline-block;
    margin: 0 5px;
    justify-content: center;
    font-weight: normal;
    font-weight: normal;
}

.App-links a:hover {
    color: #2176AE;
}

.App-links a.active {
    color: #61dafb;
}

.title {
    color: #61dafb;
}

.App-header > .title > .subtitle {

}

.subtitle {
    font-weight: normal;
    color: #ccc;
    font-family: 'Reenie Beanie', cursive !important;
    min-font-size: 16px !important;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

hr {
    border-color: #61dafb;
}

