.resume-card {
    position: relative;
    max-width: 725px;
    min-width: 725px;
    margin-left: -130px;
    padding-left: 130px;
    /*margin-top: -100px;*/
}

.resume-card > .title {
    color: #61dafb;
    margin-top: 0px;
    margin-bottom: 10px;
}

.resume-card > .title > .subtitle {
    color: #ccc;
}

.resume-card > .left {
    float:left;
    margin-top: 5px;
}

.resume-card > .right {
    text-align: left;
    font-size: 0.7em;
}
.resume-card a {
    display: inline-block;
    color: #AEECEF;
    text-decoration: none;
}

.resume-card a:hover {
    color: #2176AE;
}

.left + .right {
    margin-left: 135px;
}

.resume-card .timeline {
    float: right;
    font-size: 0.7em;
    color: #ccc;
}

.resume-card > .right > .contents {
    font-size: 0.7em;

    /* font-family: 'Zilla Slab', serif; */
    font-weight: normal;
}

.resume-card > .right > .contents .subtitle {
    font-size: 2.0em;
}

.skill-img {
    width: 25%;
    margin-right:10px;
    display: inline-block;
}
